import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';

/* Components */
import { DashboardComponent } from './dashboard/dashboard.component';
import { ProjectsComponent } from './projects/projects.component';
import { NetworksComponent } from './networks/networks.component';
import { ArticlesComponent } from './articles/articles.component';

/* Services */
import { BackofficeService } from '../../Services/Backoffice/backoffice.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';

/* Directives */
import { BackofficeDirective } from '../../Directives/Backoffice/backoffice.directive';

/* Libraries */
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';
import { FroalaEditorModule, FroalaViewModule } from 'angular2-froala-wysiwyg';
import { MomentModule } from 'angular2-moment';

/* Route */
import { ADMIN_ROUTES } from '../../Routes/backoffice.routes';
import { UniversityCampaignComponent } from './university-campaign/university-campaign.component';
import { StatisticsComponent } from './statistics/statistics.component';

@NgModule({
	imports: [
		ADMIN_ROUTES,
		CommonModule,
		RouterModule,
		PipeModule,
		FormsModule,
		Ng2CloudinaryModule,
		FileUploadModule,
		FroalaEditorModule.forRoot(),
		FroalaViewModule.forRoot(),
		MomentModule
	],
	declarations: [
		NetworksComponent,
		BackofficeDirective,
		DashboardComponent,
		ProjectsComponent,
		ArticlesComponent,
		UniversityCampaignComponent,
		StatisticsComponent
	],
	exports: [
	],
	providers: [
		BackofficeService,
		ProfilesService
	]
})
export class BackofficeModule { }



