import { Component, OnInit } from '@angular/core';

// Services
import { BackofficeService } from '../../../Services/Backoffice/backoffice.service';

@Component({
  selector: 'app-stastics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.scss']
})
export class StatisticsComponent implements OnInit {

	projects 	: Object[]
	users 		: Object[]
	stats 		: Object

	constructor(private BackofficeService: BackofficeService) { }

	ngOnInit() {
		this.getUsers();
		this.getProjects();
		this.getStats();
	}

	getUsers() {
		this.BackofficeService.getUsers().subscribe(res => {
			if (res)
				this.users = res.users
		})
	}

	getProjects() {
		this.BackofficeService.getProjects().subscribe(res => {
			if (res)
				this.projects = res.projects
		});
	}

	getStats() {
		this.BackofficeService.getStatsAnalytic().subscribe(res => {
			this.stats = res.stats
			console.log(this.stats);
		});
	}
}



