/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { DashboardComponent } from '../Components/backoffice/dashboard/dashboard.component';
import { ProjectsComponent } from '../Components/backoffice/projects/projects.component';
import { NetworksComponent } from '../Components/backoffice/networks/networks.component';
import { ArticlesComponent } from '../Components/backoffice/articles/articles.component';
import { UniversityCampaignComponent } from '../Components/backoffice/university-campaign/university-campaign.component';
import { StatisticsComponent } from '../Components/backoffice/statistics/statistics.component';

/* Services */
import { AuthenticationService } from '../Services/Authentication/authentication.service';

/* Librairies */

const routes: Routes = [
	{ path: '', canActivate: [AuthenticationService], component: DashboardComponent,
		children: [
			{ path: 'projects', component: ProjectsComponent },
			{ path: 'networks', component: NetworksComponent },
			{ path: 'articles', component: ArticlesComponent },
			{ path: 'university-campaign', component: UniversityCampaignComponent },
			{ path: 'statistics', component: StatisticsComponent }
		]
	}
];

export const ADMIN_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



