import { Component, OnInit } from '@angular/core';

import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { BackofficeService } from '../../../Services/Backoffice/backoffice.service';

@Component({
	selector: 'app-university-campaign',
	templateUrl: './university-campaign.component.html',
	styleUrls: ['./university-campaign.component.scss']
})
export class UniversityCampaignComponent implements OnInit {

	profiles 	: Object[] = []
	ambassadors : Object[] = []
	searchName 	: string

	constructor(private ProfilesService: ProfilesService,
			private BackofficeService: BackofficeService) { }

	ngOnInit() {
		this.getProfiles();
		this.getAmbassadors();
	}

	getProfiles() {
		this.ProfilesService.getProfiles().subscribe( res => {
			this.profiles 		= res.profiles;
		});
	}

	getAmbassadors() {
		this.BackofficeService.getAmbassadors().subscribe( res => {
			this.ambassadors = res.profiles
		})
	}

	setAmbassador(uid) {
		this.BackofficeService.setAmbassador({ id: uid }).subscribe( res => {
			if (res)
				this.getAmbassadors();
		})
	}

	removeAmbassador(uid) {
		this.BackofficeService.removeAmbassador({ id: uid }).subscribe( res => {
			if (res)
				this.getAmbassadors();
		})
	}

}



