import { Component, OnInit } from '@angular/core';

/* Services */
import { NetworksService } from '../../../Services/Networks/networks.service';

@Component({
	selector: 'app-networks',
	templateUrl: './networks.component.html',
	styleUrls: [
		'./networks.component.scss'
	],
	providers: [NetworksService]
})
export class NetworksComponent implements OnInit {

	networks 		: Object[]
	custom_name 	: string
	invite_link 	: string
	searchNetwork 	: string 

	constructor(private NetworksService: NetworksService) { }

	ngOnInit() {
		this.getNetworks();
	}

	getNetworks() {
		this.NetworksService.getNetworks('university').subscribe( res => {
			this.networks = res.university
		});
	}

	selectNetwork(network) {
		this.searchNetwork = network
	}


	// getNetworkInfo() {
	// 	this.NetworksService.getNetworkInvitation(token).subscribe( res => {
	// 		console.log(res);
	// 	});
	// }

	createNetworkInvitation() {
		if (this.searchNetwork && this.custom_name) {
			const body = { name: this.searchNetwork, url_name: this.custom_name, type: 'University' };

			this.NetworksService.createNetworkInvitation(body).subscribe( res => {
				this.invite_link = 'https://www.wittycircle.com/invite/welcome/' + this.custom_name + '/' + res.token
			});
		}
	}

}



