/* Article Models */

export interface ArticleModel {
	author_id		: number
	creation_date 	: Date
	id 				: number
	picture 		: string
	read_time 		: number
	tags 			: number[]
	text 			: string
	title 			: string
	view 			: number
}

export const Article = {
	title 		: '',
	picture 	: '',
	author 		: '',
	tags 		: [],
	read_time 	: 0,
	text 		: ''
}



