import { Injectable } from '@angular/core';
import { Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ArticleTagsService {

	constructor(private AuthHttp: AuthHttp) { }

	// Get all tags
	getTags() : Observable<any> {
		const url = 'http://localhost:3000/api/article_tags'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Create new tag
	createTag(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/article_tags'

		return this.AuthHttp.post(url, body)
		.map( (res:Response) => res.json())
		.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Update tag
	updateTag(body: Object, tags_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/article_tags' + tags_id

		return this.AuthHttp.put(url, body) // TODO: Only property 'name' in body or also tags_id ?
		.map( (res:Response) => { console.log(res) })
		.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Remove tag
	deleteTag(tags_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/article_tags/' + tags_id

		return this.AuthHttp.delete(url)
			.map( (res:Response) => { console.log(res) })
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



