import { Component, OnInit } from '@angular/core';

/* Services */
import { BackofficeService } from '../../../Services/Backoffice/backoffice.service';

@Component({
	selector: 'app-projects',
	templateUrl: './projects.component.html',
	styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {

	projects 	: Object[]
	name 		: string
	email 		: string
	public_id 	: string
	username 	: string
	type		: string
	types 		: Array<string> = ['new_projects', 'recently_projects', 'featured_projects', 'top_ranked_people', 'great_people']
	errors 		: Object = { error1: null, error2: null, error3: null }
	success 	: string = null

	constructor(private BackofficeService: BackofficeService) { }

	ngOnInit() {
		// this.getRecommandedProject();
	}

	addProjects() {
		const body = { 
			username: this.username || null, 
			public_id: parseInt(this.public_id) || null,
		};

		if (this.username && this.public_id)
			return this.errors['error3'] = 'only username or public id !!!!!'
		if (this.types.indexOf(this.type) < 0)
			return this.errors['error1'] = 'Wrong type'

		this.BackofficeService.addSelectedProjectsProfiles(this.type, body).subscribe(r => {
			if (r.success)
				this.success = 'Adding done!'
			else
				this.errors['error2'] = 'Adding fail, something went wrong ! My bad'
			this.removeError();
		});
	}

	removeSelectedPP() {
		const body = { 
			username: this.username || null, 
			public_id: this.public_id || null,
		};

		if (this.types.indexOf(this.type) < 0)
			return this.errors['error1'] = 'Wrong type'
		console.log(body);
		this.BackofficeService.removeSelectedProjectsProfiles(this.type, body).subscribe(r => {
			if (r.success)
				this.success = 'Removing done!'
			else
				this.errors['error2'] = 'Removing fail, something went wrong ! My bad'
			this.removeError();
		});
	}

	removeError() {
		this.errors		= { error1: null, error2: null, error3: null };
		this.type 		= null;
		this.username 	= null;
		this.public_id 	= null;
	}

	// getRecommandedProject() {
	// 	this.BackofficeService.getSelfPostProjects().subscribe( res => {
	// 		console.log(res);
	// 		this.projects = res.project_list
	// 	});
	// }

	// sendProjectInvitation(index) {
	// 	console.log(this.projects[index]);
	// 	const body = {
	// 		name 	: this.name,
	// 		id 		: this.projects[index]['id'],
	// 		email 	: this.email
	// 	}
	// 	this.BackofficeService.sendProjectInvitation(body).subscribe( res => {
	// 		console.log(res);
	// 	});
	// }

	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}
}



